import React from 'react'
import { Route } from 'react-router'
import { BrowserRouter } from 'react-router-dom'
import Landing from './Landing'
import Vids from './Vids'
import {PressPage} from './PressPage.js'
import {HallOfFame} from './HallOfFame.js'
import './App.css'

export default class App extends React.Component {
  render () {
    console.log('process.env', process.env);
    return (
      <BrowserRouter>
          <div className='app'>
            <Route path={process.env.PUBLIC_URL + '/'} exact component={Landing} />
            <Route
              path={process.env.PUBLIC_URL + '/presspage'} exact
              component={PressPage}
            />
            <Route
              path={process.env.PUBLIC_URL + '/vids'} exact
              component={Vids}
            />
            <Route
              path={process.env.PUBLIC_URL + '/halloffame'} exact
              component={HallOfFame}
            />
          </div>
      </BrowserRouter>
    )
  }
}



